import React, { useEffect, useState } from 'react'
import { NavLink } from 'react-router-dom';
import './navbar.css';

const Navbar = () => {
    const [islogin, setlogin] = useState(false)

    const [user, setuser] = useState(localStorage.getItem("user"))
    useEffect(() => {
        if (user) {
            setlogin(true)
        }
    })

    return (<>
        {islogin ?
            <div className='nav'>

                <div className="name">

                    <h1>{user}</h1>

                </div>


                <><nav><li>
                    <NavLink to='/dash/'><i class="fa-solid fa-book"></i><p></p></NavLink>
                </li> <li><NavLink to='/account'><i class="fa-solid fa-landmark"></i><p></p></NavLink></li><li><NavLink to='/chart'><i class="fa-solid fa-chart-simple"></i><p></p></NavLink></li><li><NavLink to='/setting'><i class="fa-solid fa-gear"></i><p></p></NavLink></li></nav></>


            </div>
            : ""}
    </>
    )
}

export default Navbar
