import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import "./Acc.css";
import { url } from "../service";
function Acc() {
  const navigate = useNavigate();
  const [islogin, setlogin] = useState(false);
  const [user, setuser] = useState(localStorage.getItem("user"));
  const [logmoment, setlog] = useState("");

  useEffect(() => {
    if (user) {
      navigate("/");
      window.location.reload();
    }

    console.log(user);
  }, [user]);

  var loginURL = `${url}/account_api/login.php`;
  var signURl = `${url}/account_api/sign.php`;

  const [error, setError] = useState("");
  const change = (moment) => {
    let log = document.getElementById("login");
    let sig = document.getElementById("sign");
    if (moment === "login") {
      log.classList.add("active");
      sig.classList.remove("active");
      document.getElementsByClassName("login")[0].style.display = "flex";
      document.getElementsByClassName("sign")[0].style.display = "none";
      setlog("");
      setError("");
    } else {
      log.classList.remove("active");
      sig.classList.add("active");
      document.getElementsByClassName("login")[0].style.display = "none";
      document.getElementsByClassName("sign")[0].style.display = "flex";
      setlog("");
      setError("");
    }
  };
  function hasWhitespace(input) {
    return /\s/.test(input);
  }

  const sign = () => {
    let user = document.getElementById("sig-name").value;
    let pass = document.getElementById("sig-pass").value;
    if (!hasWhitespace(user) && !hasWhitespace(pass)) {
      fetch(signURl, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          user: user,
          pass: pass,
        }),
      })
        .then((res) => res.json())
        .then((data) => {
          if (data.ok === "true") {
            setError(data.error);
            window.location.reload();
          } else {
            setError(data.error);
          }
        });
    } else {
      setError("");
    }
  };

  const login = () => {
    let user = document.getElementById("log-name").value;
    let pass = document.getElementById("log-pass").value;
    fetch(loginURL, {
      method: "POST",

      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        user: user,
        pass: pass,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        if (data.ok === "true") {
          setuser(user);
          localStorage.setItem("user", user);
          setlogin(true);
        } else {
          setError("!");
        }
      })
      .catch((err) => [console.log(err)]);
  };

  return (
    <>
      <div className="acc">
        <h1>{logmoment}</h1>
        <div className="change">
          <button
            id="login"
            className="active"
            onClick={() => {
              change("login");
            }}
          >
            
          </button>
          <button
            id="sign"
            onClick={() => {
              change("sign");
            }}
          >
            
          </button>
        </div>
        <div className="login">
          <h3></h3>
          <div className="user">
            <label htmlFor="log-name"> </label>
            <input type="text" id="log-name" placeholder="" />
          </div>
          <div className="user">
            <label htmlFor="log-pass"> </label>
            <input type="password" id="log-pass" placeholder="" />
          </div>
          <p>{error}</p>
          <button onClick={login}></button>
        </div>
        <div className="sign">
          <h3></h3>
          <div className="user">
            <label htmlFor="sig-name"> </label>
            <input type="text" id="sig-name" placeholder="" />
          </div>
          <div className="user">
            <label htmlFor="sig-pass"> </label>
            <input type="password" id="sig-pass" placeholder="" />
          </div>
          <p>{error}</p>
          <button
            onClick={() => {
              sign();
            }}
          >
            
          </button>
        </div>
      </div>
    </>
  );
}

export default Acc;
