import React from 'react'
import { NavLink } from 'react-router-dom'
import './Dashnav.css'
function Dashnav() {
    return (
        <div className='type'>
            <ul>
                <li>
                    <NavLink to='/dash/in'></NavLink>
                </li>
                <li>
                    <NavLink to='/dash/out'></NavLink>
                </li>

            </ul>
        </div>
    )
}

export default Dashnav
