import { useSearchParams } from "react-router-dom";
import Acc from "./Acc";
import Sett from "./Sett";
import React, { useState } from 'react'

export default function Account() {

    const [user, setuser] = useState(localStorage.getItem("user"));


    return (<>
        {user ? <Sett /> : <Acc />}
    </>)
}
